body {
    margin: 0;
}

canvas {
    z-index: -1;
}

#overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 24px;
    color: white;
    background-color: rgba(0, 0, 0, 0);
    pointer-events: none;
}

#inputText {
    background-color: rgba(192, 192, 192, 0.5);
    opacity: 0.8;
    width: 240px;
    border-radius: 10px;
    padding: 10px;
    box-sizing: border-box;
    font-family: Arial, sans-serif;
    font-size: 14px;
    border: 2px solid #ccc;
    color: #fff;
    pointer-events: auto;
}

#inputText::placeholder {
    color: #222;
}

.speechDiv {
    height: 38px;
    margin-left: 8px;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: right;
    align-items: right;
    pointer-events: none;
}

.speechButton {
    pointer-events: auto;
    position: relative;
    height: 38px;
    border-radius: 8px;
}

.topMenu {
    position: absolute;
    top: 0;
    left: 24px;
    width: 100%;
    height: 38px;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: space-between;
    align-items: center;
    pointer-events: auto;
}

.chooseLang {
    position: relative;
    pointer-events: auto;
}

.chooseLang div {
    height: 60px;
    position: relative;
}

.lang {
    cursor: pointer;
    display: block;
    height: 40px;
    position: absolute;
    opacity: 0;
    left: -48px;
    top: 0;
    -webkit-transition: 0.76s;
    -o-transition: 0.76s;
    transition: 0.76s;
    width: 40px;
}

.pt-lang {
    background-image: url(data:image/svg+xml;base64,);
}

.open .pt-lang {
    left: -48px;
    top: 48px;
    opacity: 1;
}

.en-lang {
    background-image: url(data:image/svg+xml;base64,);
}

.open .en-lang {
    left: -48px;
    top: 0px;
    opacity: 1;
}

.chooseLang .chosen {
    opacity: 1;
    z-index: 1;
}

.suggestions-menu {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translateX(-100%);
    width: 20%;
    min-width: 180px;
    max-width: 300px;
    height: 300px;
    /* background-color: rgba(255, 255, 255, 0.05); */
    background-color: transparent;
    border-radius: 5px;
    padding: 10px;
    z-index: 9999;
}

.suggestions-menu-header {
    font-weight: bold;
    font-size: 14px;
    margin-bottom: 10px;
}

.suggestions-menu-body {
    position: absolute;
    width: 100%;
    height: calc(100% - 50px);
    overflow-y: auto;
    background-color: transparent;
    pointer-events: auto;
}

.suggestions-menu-body::-webkit-scrollbar {
    width: 0;
    height: 0;
}

.sentence-button {
    display: block;
    font-size: 14px;
    border: 1px solid #000000;
    background-color: rgba(37, 37, 37, 0.5);
    padding: 0px 5px;
    pointer-events: auto;
}

.sentence-button:hover {
    background-color: rgba(37, 37, 37, 0.8);
    border-color: #333;
}
